
.navbar-logo {
  width: 120px;
  margin: 3px 0;
  height: auto !important;
  line-height: auto !important
}
.sidenav-logo {
  width: 40%;
  margin-top: 10px;
}
.container- {
  padding: 0 5%;
}
.preloader {
  margin-right: 25px;
}

/* text size */
.giant-text {
  font-size: 2.5rem;
}
.large-text {
  font-size: 2rem;
}
.medium-text {
  font-size: 1.5rem;
}
.normal-text {
  font-size: 1rem;
}

/* input text */
.input-field input:focus+label,
.input-field textarea:focus+label {
  color: #3f51b5 !important;
}
.input-field input.valid,
.input-field textarea.valid {
  border-bottom: 1px solid #3f51b5 !important;
  -webkit-box-shadow: 0 1px 0 0 #3f51b5 !important;
          box-shadow: 0 1px 0 0 #3f51b5 !important;
}

/* label underline focus color */
.input-field input:focus,
.input-field textarea:focus {
  border-bottom: 1px solid #3f51b5 !important;
  -webkit-box-shadow: 0 1px 0 0 #3f51b5 !important;
          box-shadow: 0 1px 0 0 #3f51b5 !important;
}
.input-field .prefix.active,
.input-field .prefix.active {
  color: #3f51b5;
}

/* border radius in components */
.card,
.card-panel,
.btn,
.btn-flat {
  border-radius: 1.5rem;
}
.modal {
  border-radius: 1rem;
}

/* buttons height auto */
.btn,
.btn-flat,
.btn-large {
  height: auto;
}
header,
main,
footer {
  padding-left: 300px;
}
@media only screen and (max-width : 992px) {
header,
  main,
  footer {
    padding-left: 0;
}
}





































































































